export default {
	data() {
		return {
			title: 'Hello',
			num18:this.num17
		}
	},
	props:['num17'],
	onLoad() {
		// this.num18 = this.num17;
	},
	methods: {
		plus(){
			this.num18++;
			
		},
		down(){
			this.num18--;
			
		},
		setNum(num){
			this.num18 = num
		}
	},
	
	watch:{
		num18(newval,oldval){
			if(newval <= 0){
				this.num18 = 1
				uni.showToast({
					"title":"数量必须大于等于1哦",
					"icon":"none"
				})
				
			}
			this.$emit("getValue",this.num18)
		}
	}
}
